___CSS_LOADER_EXPORT___.push([module.id, `.add-list-modal {
  border: none;
  border-radius: 10px;
  padding: 40px;
  width: 500px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);

  &:open {
    animation: dialog-fade-in 0.3s ease-out;
  }

  &:open::backdrop {
    animation: backdrop-fade-in 0.3s ease-out forwards;
  }
}

.add-list-form {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.add-list-form .close-modal {
  align-self: flex-end;
  background: none;
  border: none;
  outline: none;
  border-radius: 6px;
  padding: 5px;
  text-decoration: underline;
  font-weight: var(--font-weight-semibold);
  transition: color 0.3s;
  cursor: pointer;

  &:focus-visible {
    outline: 2px solid var(--color-black);
  }

  &:active {
    opacity: 0.7;
  }

  &:hover {
    color: var(--color-bg-primary);
  }
}

.modal-add-button {
  align-self: flex-end;
  font-size: var(--font-size-base);
}

.list-title {
  border: none;
  outline: none;
  padding: 10px;
  border-radius: 10px;
  font-size: 2.5rem;

  &:focus {
    outline: 2px solid var(--color-black);
  }
}
