___CSS_LOADER_EXPORT___.push([module.id, `.list-switcher-container {
  position: relative;
  transition: background-color 0.3s;
}

.list-switcher-container:hover .list-select-button:not(:disabled) {
  background-color: var(--color-bg-secondary);

  svg {
    animation: shake 0.6s ease-in-out;
  }
}

.list-select-button {
  display: flex;
  align-items: center;
  column-gap: 16px;
  width: calc(100% - 4px);
  margin: 2px auto 10px;
  padding: 20px;
  padding-right: 50px;
  border: none;
  background: none;
  outline: none;
  border-radius: 14px;
  text-align: left;
  color: var(--color-white);
  cursor: pointer;
  transition: background-color 0.3s;
  z-index: 1;

  svg {
    min-width: 24px;
    fill: currentColor;
    transform-origin: top center;
  }

  &:not(:disabled):focus {
    outline: 2px solid var(--color-status-in-progress);
  }

  &:disabled {
    cursor: auto;
  }
}

.list-select-button--active {
  background-color: var(--color-white);
  color: var(--color-bg-primary);
}

.list-actions-button {
  position: absolute;
  display: none;
  place-content: center;
  padding: 0;
  width: 30px;
  height: 30px;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: var(--color-white);
  border: none;
  background: none;
  outline: none;
  z-index: 2;
  cursor: pointer;
  transition: color 0.3s, transform 0.3s;
  pointer-events: auto;

  svg {
    width: 24px;
    height: 24px;
    fill: currentColor;
  }

  &:hover {
    color: var(--color-status-not-started);
  }

  &:active {
    transform: translateY(calc(-50% + 2px));
  }
}

.list-select-button--active + .list-actions-button {
  color: var(--color-bg-primary);

  &:hover {
    color: var(--color-status-not-started);
  }
}

.list-switcher-container:hover:not(:has(.list-actions-modal:hover))
  .list-actions-button {
  display: grid;
}

@keyframes shake {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(-10deg);
  }
  75% {
    transform: rotate(5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
